import SweetScroll from "sweet-scroll";

/**
 * スムーススクロールのインスタンス
 */
export default class Scroll {
  /**
   * @classdesc SweetScrollを用いてスムーススクロールさせる
   */
  constructor() {
    this.eventBind();
  }

  /**
   * @description スムーススクロールの発火
   */
  eventBind() {
    new SweetScroll({
      trigger: "a[href^='#']",
      offset: -110,
    });
  }
}
