/**
 * リンクを変えるだけのインスタンス
 */
export default class EnviromentLinkChange {
  /**
   * @classdesc dev、本番などでボタンのURLを変える
   */
  constructor() {
    this.getParam();
    this.eventBind();
  }

  /**
   * @description 要素の取得
   */
  getParam() {
    this.elwrap = document.querySelector('[data-module="btn-link"]');
    if (this.elwrap) {
      this.elm = document.querySelector('[data-module="btn-link"]');
      this.elmLink = this.elm.href;
      this.environment = location.hostname;
    }
  }

  /**
   * @description 各環境ごとにリンクを変更する
   */
  eventBind() {
    const prod = "store.kinto-jp.com";
    const stg = "stg-store-www.kinto-jp.com";
    const dev = "devtest3-store-www.kinto-jp.com";
    const service = "kinto-jp.com/customer/";
    const servicestg = "stg-www.kinto-jp.com/customer/";
    const servicedev = "devtest3-www.kinto-jp.com/customer/";
    const param = "dealer-site-redirect?dlrflg=true";

    const local = "localhost";
    const localservice = "localhost/customer/";

    if (this.elm) {
      if (this.environment === prod) {
        this.elm.setAttribute("href", `https://${service}${param}`);
      } else if (this.environment === stg) {
        this.elm.setAttribute("href", `https://${servicestg}${param}`);
      } else if (this.environment === dev) {
        this.elm.setAttribute("href", `https://${servicedev}${param}`);
      } else if (this.environment === local) {
        this.elm.setAttribute("href", `https://${localservice}${param}`);
      }
    }
  }
}
